




















import { Component, Vue } from 'vue-property-decorator';
import axios from 'axios';

@Component({
  components: {},
})
export default class Home extends Vue {
  private clientIp = '';

  private onSubmit() {
    axios.post('/api/test/send', { clientIp: this.clientIp }).then((res: any) => {
      console.log('success!', res);
    });
  }
}
